/**
 * Created by gaolei on 2017/4/21.
 *
 * 月新增落地站点明细报表-月
 */
import React from 'react';
import {connect} from 'react-redux';
import CenterTopNav from '../../components/public/centerTopNav';
import {browserHistory } from 'react-router';
import Pagination from '../../components/public/pagination';
import {dialogHandle} from '../../actions/dialog';
import SelectBox from '../../components/public/selectBox';
import ReportFormTable from '../../components/public/reportFormTable';
import DepartmentTree from '../../components/public/department';
import {reportFormCommonHandle,queryProvince,queryCity,queryCounty,getBankTypeCodes,getCompanyByCompanyType} from '../../actions/reportForm/public/common';
import {siteNewFinishDataMonthHandle,siteNewFinishDataMonthLoadData,siteNewFinishDataMonthExport} from '../../actions/reportForm/siteNewFinishDataMonth';
import Regional from '../../components/reportForm/regional';
import ExportButton from '../../components/reportForm/exportButton';


class siteNewFinishDataMonthMain extends React.Component {

    componentWillUnmount(){
        $('body').removeClass('select-data-month');
    }

	componentDidMount(){
		$('body').addClass('select-data-month');
		this.resetFun(true);
        var _that = this;
        $(function() {        	
            $( "#datepicker" ).datepicker({
                maxDate:-1,
                monthNamesShort: ["1月", "2月", "3月", "4月", "5月", "6月", "7月", "8月", "9月", "10月", "11月", "12月"],  // 区域化月名为中文
                prevText: '上月',         // 前选按钮提示
                nextText: '下月',         // 后选按钮提示
                changeYear: true,          // 年下拉菜单
                changeMonth: true,             // 月下拉菜单
                showButtonPanel: true,         // 显示按钮面板
                showMonthAfterYear: true,  // 月份显示在年后面
                currentText: "本月",         // 当前日期按钮提示文字
                closeText: "关闭",           // 关闭按钮提示文字
                dateFormat: "yy-mm",       // 日期格式
                onClose: function(dateText, inst) {// 关闭事件
                    var month = $("#ui-datepicker-div .ui-datepicker-month :selected").val();
                    var year = $("#ui-datepicker-div .ui-datepicker-year :selected").val();
                    _that.props._siteNewFinishDataMonthHandle({
                        reportDate: year+'-'+(Number(month)>=9?Number(month)+1:'0'+(Number(month)+1))
                    });
                    $(this).datepicker('setDate', new Date(year, month, 1));
                }
           });
        });

		this.props._queryProvince({
            reportName:"nodedropdataincMonthly"
        });
        this.props._getBankTypeCodes({
            reportName:"nodedropdataincMonthlyMonth"
        });
        this.props._getCompanyByCompanyType();
    }
	//加载分页插件
    loadPage(options) {
        this.props._siteNewFinishDataMonthHandle({
            loadPageFlag: false
        });
        this.pageNavClick(1,10,options);
    }
	//调用分页加载
    pageNavClick(n,pageSize,options) {
        this.props._siteNewFinishDataMonthHandle({
            curPage: n,
            pageLimit:pageSize
        });
        this.loadData(n,pageSize,options?options:this.props.siteNewFinishDataMonth.temp_options);
    }
    //加载数据
    loadData(n,pageSize,options){
    	//加载中
        this.props._siteNewFinishDataMonthHandle({
            curPage: n,
            pageLimit:pageSize
        });       
        
        let ajaxData = {
        	curPage: n,//当前页
            pageSize: pageSize,//每页多少行
            reportType:"1",//报表类型(日/月)
            reportDate: options.reportDate+'-01',//数据日期
            provinceName:options.province.name=="全部"?"":options.province.name,//省
			cityName:options.city.name=="全部"?"":options.city.name,//市
			countyName:options.county.name=="全部"?"":options.county.name,//区县
			companyName:options.department.val,//部门
            bankTypeCode: options.bank,//银行code
            nodeName: options.siteName,//站点啊名称
            nodeCode: options.siteNum,//站点编号
        };
    	this.props._siteNewFinishDataMonthLoadData({
    		data:ajaxData
        });
    }
    //省份
    selectClick(id,val,name){
        switch(id){
            case 1 :
               this.props._siteNewFinishDataMonthHandle({
                   province:{val:val,name:name},
                   city:{val:'',name:''},//选中的城市
                   county:{val:'',name:''}//选中的县
               })

               if(val==''){
                  //用户选择全部的情况

                   //重置市县区
                   this.props._reportFormCommonHandle({
                       cityList:[],
                       countyList:[]
                   })
               }else{
                   //重置县区
                   this.props._reportFormCommonHandle({
                       countyList:[]
                   })
                   //更新城市数据
                   this.props._queryCity({
                       provinceCode:val,
                       reportName:"nodedropdataincMonthly"
                   })
               }

               break;
            case 2 :
                this.props._siteNewFinishDataMonthHandle({
                    city:{val:val,name:name},
                    county:{val:'',name:''}//选中的县
                })
                if(val==''){
                    //用户选择全部的情况

                    //重置县区
                    this.props._reportFormCommonHandle({
                        countyList:[]
                    })
                }else{
                    //更新县区数据
                    this.props._queryCounty({
                        cityCode:val,
                        reportName:"nodedropdataincMonthly"
                    })
                }

                break;
            case 3 :
                this.props._siteNewFinishDataMonthHandle({
                    county:{val:val,name:name}
                })
                break;
            case 4 :
                this.props._siteNewFinishDataMonthHandle({
                    bank:val
                })
                break;
            default :
                break;
        }
    }
    showProvince(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.provinceList.map((val,key)=>{
             temp_arr.push({value:val.districtCode,name:val.districtName})
        });
        return arr.concat(temp_arr);
    }

    showCity(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.cityList.map((val,key)=>{
            temp_arr.push({value:val.districtCode,name:val.cityName})
        });
        return arr.concat(temp_arr);
    }

    showCounty(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.countyList.map((val,key)=>{
            temp_arr.push({value:val.districtCode,name:val.countyName})
        });
        return arr.concat(temp_arr);
    }

    showBank(){
        let arr=[{value:"",name:"全部"}];
        let temp_arr=[];
        this.props.reportFormCommon.bankList.map((val,key)=>{
            temp_arr.push({value:val.bankTypeCode,name:val.bankTypeName})
        });
        return arr.concat(temp_arr);
    }
    
    //部门
    departmentReset(value,name){
    	this.props._siteNewFinishDataMonthHandle({
			department:{val:value,name:name}
		});
    }
    
    //站点名称
    siteNameFun(event){
    	this.props._siteNewFinishDataMonthHandle({
			siteName:event.target.value
		});
    }
    //站点编号
    siteNumFun(event){
    	
    	this.props._siteNewFinishDataMonthHandle({
			siteNum:event.target.value
		});
    }
    
    
	
	//查询
	queryData(){
		if(this.props.siteNewFinishDataMonth.tbodyList=='loading'){
			this.props._dialogHandle({
                type: "tips",
                time: 2000,
                content: "正在查询中，请耐心等待！",
                tipsType: "warning",
                show: true
            })
		}else{
			let siteNumCheck=$.XlCheck({
				val:this.props.siteNewFinishDataMonth.siteNum,
				rule:["Number"] 
			});
			if(!siteNumCheck.Number){
				this.props._dialogHandle({
					show:true,
					content:'站点编号只能输入数字',
					type:"tips",
					tipsType:"warning"
				});
				return false;
			}			
			let options = {
				reportDate:this.props.siteNewFinishDataMonth.reportDate,//数据日期
		        province:this.props.siteNewFinishDataMonth.province,//选中的省
			    city:this.props.siteNewFinishDataMonth.city,//选中的城市
			    county:this.props.siteNewFinishDataMonth.county,//选中的县
		        department:this.props.siteNewFinishDataMonth.department,//部门code
		        bank:this.props.siteNewFinishDataMonth.bank,//银行code
		        siteName:this.props.siteNewFinishDataMonth.siteName,//站点名称
		        siteNum:this.props.siteNewFinishDataMonth.siteNum,//站点编号
			};
			this.props._siteNewFinishDataMonthHandle({
				tbodyList: 'loading',
				temp_options:options
			});
			this.loadData(1,this.props.siteNewFinishDataMonth.pageLimit,options);
		}
		
	}
	//重置

	resetFun(bool){
		if(bool===true){
			let options={
                reportDate: moment().subtract(1,"days").format('YYYY-MM'),//默认日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
          	};
           //初次进入页面，所有参数初始化
           this.props._siteNewFinishDataMonthHandle({
                reportDate: moment().subtract(1,"days").format('YYYY-MM'),//默认日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
                temp_options:options
           	});
           //进入页面发起默认请求加载数据
           this.loadPage(options);
		}else{
			this.props._siteNewFinishDataMonthHandle({
	            reportDate: moment().subtract(1,"days").format('YYYY-MM'),//默认日期
	            province:{val:"",name:""},//省
				city:{val:"",name:""},//市
				county:{val:"",name:""},//区县
	            department:{val:"",name:""},//部门            
	            bank: "",//银行code
	            siteName: "",//站点名称
	            siteNum: "",//站点编号
	        });
		}
		this.selectClick(1,'','');
		
	}
	//导出报表
    exportExcel(){
        let options=this.props.siteNewFinishDataMonth.temp_options;
        this.props._siteNewFinishDataMonthExport({
            reportType:"1",//报表类型(日/月)
            reportDate: options.reportDate+'-01',//数据日期
            provinceName:options.province.name=="全部"?"":options.province.name,//省
			cityName:options.city.name=="全部"?"":options.city.name,//市
			countyName:options.county.name=="全部"?"":options.county.name,//区县
			companyName:options.department.val,//部门
            bankTypeCode: options.bank,//银行code
            nodeName: options.siteName,//站点啊名称
            nodeCode: options.siteNum,//站点编号
        });
    }

    render() {
        return (
            <div className="height100p">
                <CenterTopNav title="月新增落地站点明细月报" parentList={[{name:"数据报表"},{name:"银行业务月报"}]} />
                <div className="height100pY plr26">
					{/*form content begin*/}
					<div className="pub-form-top clearfix">
						<ul>
							<li>
								<span>数据日期:</span>
								<input id="datepicker" type="text" value={this.props.siteNewFinishDataMonth.reportDate} readOnly />
							</li>						
							<Regional
                                selectClick={this.selectClick.bind(this)}
                                provinceVal={this.props.siteNewFinishDataMonth.province.val}
                                cityVal={this.props.siteNewFinishDataMonth.city.val}
                                countyVal={this.props.siteNewFinishDataMonth.county.val}
                                provinceList={this.showProvince()}
                                cityList={this.showCity()}
                                countyList={this.showCounty()}
                            />
						</ul>
						<ul>
							<li>
								<span>所属部门:</span>
								<DepartmentTree value={this.props.siteNewFinishDataMonth.department.name}
	                                departmentData={this.props.reportFormCommon.companyList}
	                                callBack={this.departmentReset.bind(this)}
	                            />
							</li>
							<li>
                                <span>合作银行:</span>
                                <SelectBox
                                    callBack={this.selectClick.bind(this,4)}
                                    value={this.props.siteNewFinishDataMonth.bank}
                                    list={this.showBank()}
                                />
                            </li>
							<li>
								<span>站点名称:</span>
								<input type="text" placeholder="请输入站点名称" onChange={this.siteNameFun.bind(this)} value={this.props.siteNewFinishDataMonth.siteName} />
							</li>
							<li>
								<span>站点编号:</span>
								<input type="text" placeholder="请输入站点编号" onChange={this.siteNumFun.bind(this)}  value={this.props.siteNewFinishDataMonth.siteNum}  />
							</li>
						</ul>
						
						<div className="pub-form-btns clearfix">
							<div className="left">
								<button onClick={this.queryData.bind(this)}>查询</button>
								<button onClick={this.resetFun.bind(this)}>重置</button>
							</div>
							<ExportButton
                               data={this.props.login.Jurisdiction.ReportForm.btns.siteNewFinishDataMonth}
                               clickBack={this.exportExcel.bind(this)}
                            />							
						</div>
						
					</div>
					{/*form content end*/}
					
					{/*content begin*/}
					<div className="form-content-wrap">
						<div className="form-table-wrap">
						   <ReportFormTable
						     dataList={this.props.siteNewFinishDataMonth.tableList}
							 titleList={this.props.siteNewFinishDataMonth.tableTitle}
							 colspan={13}
							 status={this.props.siteNewFinishDataMonth.tbodyList}
						   />		
							
						</div>
						<Pagination
	                        curPage={this.props.siteNewFinishDataMonth.curPage}
	                        totalNumber={this.props.siteNewFinishDataMonth.totalSize}
	                        pageLimt={this.props.siteNewFinishDataMonth.pageLimit}
	                        pageClick={this.pageNavClick.bind(this)}
	                    />
						
					</div>
					{/*content end*/}
					
                </div>
            </div>
        )
    }
}

function mapStateToProps(state){
    return state;
}
function mapDispatchToProps(dispatch){
    return { 
    	_siteNewFinishDataMonthHandle:(options)=>{
            dispatch(siteNewFinishDataMonthHandle(options));
        },
        _siteNewFinishDataMonthLoadData:(options)=>{
       		dispatch(siteNewFinishDataMonthLoadData(options))
        },
        _queryProvince:(options)=>{
            dispatch(queryProvince(options));
        },
        _queryCity:(options)=>{
            dispatch(queryCity(options));
        },
        _queryCounty:(options)=>{
            dispatch(queryCounty(options));
        },
        _getBankTypeCodes:(options)=>{
        	dispatch(getBankTypeCodes(options))
        },
        _getCompanyByCompanyType:(options)=>{
        	dispatch(getCompanyByCompanyType(options))
        },
        _reportFormCommonHandle:(options)=>{
        	dispatch(reportFormCommonHandle(options))
        },
        _dialogHandle:(options)=>{
        	dispatch(dialogHandle(options))
        },
        _siteNewFinishDataMonthExport:(options)=>{
        	dispatch(siteNewFinishDataMonthExport(options))
        },
        
    }
}
const siteNewFinishDataMonth = connect(
	mapStateToProps,
    mapDispatchToProps
)(siteNewFinishDataMonthMain);

export default  siteNewFinishDataMonth;